.slotSelector{
    justify-content: center;
    align-items: center;
}

.slotsCont{
    width: 100%;
    display: flex;
    justify-content: center;
    gap: 50px;
}

.slotsCont > textarea, textarea:active, textarea:focus{
    width: 90%;
    height: 200px;

    resize: none;

    font-size: 16px;
    font-family: inter;

    border-radius: 10px;
    border: 0px solid #36BBBD;
    outline: 2px solid #36BBBD;

    box-sizing: border-box;
    padding: 10px 10px;

    display: block;
}

.slotsCont:hover > :not(:hover){
    opacity: 80%;
}

.slotHead{
    margin-bottom: 40px;
}

.slots{
    height: fit-content;

    width: 175px;
    height: 100px;
    box-shadow: 2px 2px 5px rgb(156, 156, 156);
    border-radius: 10px;

    cursor: pointer;

    transition-duration: 200ms;
}

.slots:hover{
    transform: translateY(-5px);
    filter: brightness(95%) contrast(105%);
}

.slots > .date{
    background-color: #36BBBD;
    width: 100%;
    height: 50%;

    display: flex;
    justify-content: center;
    align-items: center;

    border-radius: 10px 10px 0 0;

    color: white;
    font-family: inter;
    font-weight: bold;
}

.selectedSlot > .date{
    background-color: rgb(255, 0, 76);
}

.slots > .time{
    width: 100%;
    height: 50%;
    background-color: white;

    display: flex;
    justify-content: center;
    align-items: center;

    border-radius: 0 0 10px 10px;
}

.slotSubmit{
    height: 46px;
    background-color: #36BBBD;
    border: 0px solid;
    width: 90%;

    align-self: center;

    font-family: inter;
    font-weight: bold;
    font-size: 16px;
    letter-spacing: 2px;
    cursor: pointer;
    margin-bottom: 20px;

    color: white;
}

.slotBtnContainer{
    display: flex;
    width: 90%;
    gap: 20px;
}