@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");
 
 .bg {
    margin:0;
    height: 100vh;
    font-family: "Poppins", sans-serif;
    padding:0;
    background: linear-gradient( #86c0e2,#39bfe0);
    letter-spacing: 1px;
  }
  
  .LoginBox {
    position: absolute;
    top: 45%;
    left: 51%;
    width: 25%;
    padding: 40px;
    transform: translate(-50%, -50%);
    background: rgba(120, 161, 173, 0.5);
    box-sizing: border-box;
    box-shadow: 0 15px 25px rgba(0,0,0,.6);
    border-radius: 10px;
    min-height: 40vh;
  }
  
  .LoginBox h1 {
    font-size: 36px;
    padding: 0;
    color: #fff;
    text-align: center;
    text-align: start;
    margin-bottom: 15%;
 }
  
  .LoginBox .UserBox {
    position: relative;
  }
  .UserBox{
    margin-block:4% ;
  }
  .LoginBox .UserBox input {
    width: 100%;
    padding: 10px 0;
    font-size: 16px;
    color: #fff;
    margin-bottom: 30px;
    border: none;
    border-bottom: 1px solid #fff;
    outline: none;
    background: transparent;
    letter-spacing: 1px;
  }
  .LoginBox .UserBox label {
    position: absolute;
    top:0;
    left: 0;
    padding: 10px 0;
    font-size: 16px;
    color: #fff;
    pointer-events: none;
    transition: .5s;
  }
  
  .LoginBox .UserBox input:focus ~ label,
  .LoginBox .UserBox input:valid ~ label {
    top: -20px;
    left: 0;
    color: #ffffff;
    font-size: 12px;
  }
  
  
  .LoginBox .MisMatch input:valid ~ label,
  .LoginBox .MisMatch input:focus ~ label
  {
    color: rgb(253, 0, 0);
  }
  
  .LoginBox .MisMatch input {
   border-bottom: 1px solid rgb(253, 0, 0);
   color: rgb(253, 0, 0);
  }




.submitButt,.disabledButt{
    padding: 2% 4%;
    font-size: 18px;
    border: #fff 2px solid;
    cursor: pointer;
    border-radius: 6px;
    background: transparent;
    width: 40%;
    color: #fff;
    margin-block:3% ;
    transition: ease-in-out 150ms;
}

.submitButt:hover{
    scale: 1.1;
    background: white;
    color:#39bfe0;
}

.disabledButt{
    cursor: default;
    width: 40%;
    filter:opacity(40%);
}



  /* LOADER */
  .loader {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    display: inline-block;
    position: relative;
    border: 3px solid;
    border-color: #fff #fff transparent transparent;
    box-sizing: border-box;
    animation: rotation 1s linear infinite;
    scale: 0.8;
  }
  .loader::after,
  .loader::before {
    content: '';  
    box-sizing: border-box;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    border: 3px solid;
    border-color: transparent transparent #fff #fff;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    box-sizing: border-box;
    animation: rotationBack 0.5s linear infinite;
    transform-origin: center center;
  }
  .loader::before {
    width: 32px;
    height: 32px;
    border-color: #fff #fff transparent transparent;
    animation: rotation 1.5s linear infinite;
  }
      
  @keyframes rotation {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  } 
  @keyframes rotationBack {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(-360deg);
    }
  }
      


.Msg,.false{
    color: rgb(106, 255, 0);
    font-size: 20px;
    font-weight: 500;
}
.false{
   color: rgb(253, 0, 0);
}