
.newApps{
    display: flex;
}

.content {
padding: 0;
margin-top: 0;
width: 80vw;
height: 100vh;
overflow-y: scroll; /* or overflow-y: auto; */
}

.applicationsHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2% 5%;
  box-shadow: inset 0 0 5px #ddd;
  background: url('../Assets/BGbb.png');
  background-repeat:no-repeat ;
  background-size: 20;
  background-position: right bottom;
}


.heading{
font-size: 40px;
}

.searchBar {
padding: 10px 20px;
border-radius: 20px;
background-color: #f4f4f4;
display: flex;
align-items: center;
}

.searchIcon {
margin-right: 10px;
color: #737373;
}

.searchInput {
background-color: #f4f4f4;
border: none;
padding: 10px;
font-size: 16px;
border-radius: 5px;
outline: none;
}

.applicationsList {
flex: 1;
overflow-y: auto;
}

.applications{
height:83vh;
overflow-y: auto;
padding: 1% 1%;
}

.applicationItem {
margin: 10px;
border-radius: 20px;
background-color: #f4f4f4;
display: flex;
align-items: center;
justify-content: space-between;
padding: 15px;
border-bottom: 1px solid #ddd;
cursor: pointer;
}

.applicationItem img {
border-radius: 50%;
width: 8%;
height: auto;
padding: 10px;
}

.userName {
width: 35%;
padding: 10px 30px;
white-space: nowrap;
overflow: hidden;
text-overflow: ellipsis;
font-size: 20px;
font-weight: 600;
}

.userRollno {
width: 20%;
padding: 10px 30px;
font-size: 19px;
font-weight: 400;
}



.applicationLink {
color: black;
text-decoration: none;
}

.applicationLink:hover {
text-decoration: none;
}

::-webkit-scrollbar-thumb {
background: #888;
}







.buttCont{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-top: 48vh;
}

.submitButt{
  width: 70%;
  padding: 5px 10px;
  margin: 0% 0% 2% 0;
  font-size: 19px;
  font-weight: 700;
  cursor: pointer;
  background-color: #fff;
  color: #36bbbd;
  border: 3px #36bbbd solid;
  border-radius: 15px;
  transition: ease-in-out 100ms;
  
}

.submitButt:hover{
  scale:1.08;
  background-color: #36bbbd;
  color: white;
  box-shadow: 0 0 10px #36bbbd;
}

























/* LOADER */

.loaderCont{
display: flex;
align-items: center;
justify-content: center;
height: 77vh;
}
.loader {
width: 48px;
height: 48px;
border-radius: 50%;
display: inline-block;
position: relative;
border: 3px solid;
border-color: #36BBBD #36BBBD transparent transparent;
box-sizing: border-box;
animation: rotation 1s linear infinite;
scale: 1.5;
}
.loader::after,
.loader::before {
content: '';  
box-sizing: border-box;
position: absolute;
left: 0;
right: 0;
top: 0;
bottom: 0;
margin: auto;
border: 3px solid;
border-color: transparent transparent #FF3D00 #FF3D00;
width: 40px;
height: 40px;
border-radius: 50%;
box-sizing: border-box;
animation: rotationBack 0.5s linear infinite;
transform-origin: center center;
}
.loader::before {
width: 32px;
height: 32px;
border-color: #36BBBD #36BBBD transparent transparent;
animation: rotation 1.5s linear infinite;
}
  
@keyframes rotation {
0% {
  transform: rotate(0deg);
}
100% {
  transform: rotate(360deg);
}
} 
@keyframes rotationBack {
0% {
  transform: rotate(0deg);
}
100% {
  transform: rotate(-360deg);
}
}
  


















