:root{
    --topHeight: 38vh;
}

.profile{
    /* overflow-x: hidden; */
    position: relative;
    background-color: #ebebff;
    min-height: 100vh;
    width: 100%;
}

.profileBanner{
    position: absolute;
    height: var(--topHeight);
    width: 100%;

    object-fit: cover;
    object-position: center;
    filter: brightness(20%);
}

.profileContent{
    position: absolute;
    padding: 0px 100px;
    width: calc(100% - 200px);

    display: flex;
    gap: 75px;
}

.profileTop{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-width: 450px;
}

.profilePic{
    object-fit: cover;
    border-radius: 100%;
    margin-top: calc(var(--topHeight) - 30vh);
    box-shadow: 0 0 30px 0px black;
}

.profileTopInfo{
    font-family: sans-serif;
    color: black;
    height: fit-content;
    text-align: center;
    margin-top: 10px;
}

.profileTopInfo > h1{
    font-size: 40px;
}

.profileTopInfo > p{
    font-size: 16px;
}

.profileTopButtons{
    margin-top: 20px;
    display: flex;
    justify-content: center;
    gap: 10px;
    flex-wrap: wrap;
}

.profileBtn{
    border: 0px solid;
    font-size: 17px;
    display: flex;
    justify-content: center;
    align-items: center;


    font-family: sans-serif;
    font-weight: bold;
    color: white;
    text-align: center;

    padding: 13px 30px;
    border-radius: 50px;

    cursor: pointer;
    transition-duration: 200ms;
}

.profileBtn:hover{
    transform: translateY(-3px);
    filter: brightness(80%) contrast(150%);
}

.profileStackContainer{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 20px;
    max-width: 400px;
    color: white;
    gap: 10px;
}

.profileStack{
    font-size: 15px;
    width: fit-content;
    padding: 5px 15px;
    border-radius: 100px;
    text-wrap: nowrap;
    background-color: orangered;

    font-family: inter;
    font-weight: 400;
}

.profileDate{
    font-family: inter;
    margin-bottom: 20px;
}


.profileBottom{
    display: flex;
    flex-direction: column;
    margin-top: calc(var(--topHeight) + 50px);
}

.profileAbout{
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;

    width: 900px;
}

.profileSlotDisplay{
    height: 80px;
    display: flex;
    border-radius: 3px;

    font-family: inter;
}

.profileSlotDisplay > .date{
    aspect-ratio: 1;
    height: inherit;
    font-family: inter;
    background-color: #2a3139;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    color: white;
}

.profileSlotDisplay > .time{

    display: flex;
    flex-direction: column;
    justify-content: center;

    padding-left: 20px;
    padding-right: 20px;

    color: #2a3139;
    background-color: white;
}

.profileSlotDisplay > .date > span:nth-child(1){
    font-size: 26px;
    font-weight: bold;
    line-height: 25px;
}

.profileSlotDisplay > .date > span:nth-child(2){
    font-size: 18px;
    text-transform: uppercase;
}


.profileSlotDisplay > .time > span:nth-child(1){
    font-size: 26px;
    line-height: 25px;
    letter-spacing: 2px;
}

.profileSlotDisplay > .time > span:nth-child(2){
    color: #9c9c9c;
}

.profileSlotDisplay > .time > span:nth-child(1) > span:nth-child(1){
    font-weight: bold;
}

.profileSlotDisplay > .time > span:nth-child(1) > span:nth-child(2){
    font-weight: light;
}

.profileSlotDisplay > .time > span:nth-child(1) > span:nth-child(3){
    font-weight: light;
    font-size: 14px;
    text-transform: lowercase;
    letter-spacing: 0px;
}

.profileSlotDisplay > .time > span:nth-child(2){
    font-weight: light;
    font-size: 14px;
}

.profileStatus{
    background-color: #36BBBD;

    display: flex;
    justify-content: center;
    align-items: center;


    font-family: inter;
    font-weight: bold;
    color: white;
    text-align: center;
    width: 100%;
    height: 46px;

    cursor: pointer;
    transition-duration: 200ms;

    width: 100%;

}

.profileSlotDisplay > a{
    flex-grow: 1;
}

.special{

    background-color: #ff0062;

    display: flex;
    justify-content: center;
    align-items: center;

    font-family: inter;
    font-weight: bold;
    color: white;
    text-align: center;


    height: 100%;
    flex-grow: 1;
}

.profileAbout > p{
    color: gray;
}

.profileLock{
    font-size: 12px;
    margin-left: 5px;
}

.seperator{
    width: 100px;
    height: 5px;
    background-color: #36BBBD;
    border: 0px solid;
    border-radius: 2px;
    margin: 15px 0;
}





/* 1920 */

.profilePicContainer > img{
    width: 350px;
    height: 350px;
}

.profileTopInfo > h1{
    font-size: 50px;
}

.profileTopInfo > p{
    font-size: 20px;
}

.profileTop{
    width: 700px;
}

.profileStackContainer{
    max-width: 500px;
}

.profileStack{
    font-size: 20px;
    padding: 10px 20px;
}

.profileBtn{
    font-size: 20px;
}

.profileAbout > h4, p{
    font-size: 20px;
}

.profileStatus{
    font-size: 20px;
    height: 60px;
}

.profileAbout > hr{
    width: 30%;
}


@media screen and (min-width: 600px){

    .profileStackContainer{
        height: inherit;
    }

    .profileContent{
        padding: 0px 0px;
        width: 100%;
    }

}

@media screen and (max-width: 900px){

    .profileContent{
        padding: 0px 0px;
        width: 100%;
    }

    .profileStatus{
        width: 400px;
    }

    .profileBottom{
        justify-content: center;
        align-items: center;
        width: 400px;
    }

    .profileAbout{
        width: 400px;
    }

}

@media screen and (max-width: 1150px){

    .profile{
        height: 150vh;
    }
    .profileTop{
        width: 100%;
    }
    .profileContent{
        flex-direction: column;
        align-items: center;
    }
    .profileBottom{
        margin-top: -10px;
        text-align: center;
    }

    .profileAbout{
        align-items: center;
    }
    .profileAbout > hr{
        width: 40%;
    }

    .profileBottom{
        justify-content: center;
        align-items: center;
        width: 800px;
    }

}

@media screen and (max-width: 1366px){
    .profileAbout{
        width: 500px;
    }
    .profileBottom{
        width: 500px;
    }
}

