*{
  margin: 0px;
  padding: 0px;
}

body{
  font-family: sans-serif;
}

@font-face {
  font-family: inter;
  src: url("./Assets/Inter.ttf");
}

.button{
  padding: 12px 25px;
  border-radius: 10px;
  color: white;

  font-size: small;
  letter-spacing: 2px;
  text-transform: capitalize;
  cursor: pointer;

  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.button:hover{
  filter: brightness(95%);
  transform: translateY(-3px);
}


::-webkit-scrollbar {
  width: 7px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #cacaca;
  border-radius: 100px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  border-radius: 100px;
  background: #36BBBD;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #36BBBD
}

p{
  margin: 0%;
  padding: 0%;
  font-size: 16px;
}

h3{
  font-size: 20px
}
