@import url("https://fonts.googleapis.com/css2?family=Inter");

/* Reset some default styles for the body */
body {
  margin: 0;
  padding: 0;
  font-family: "Inter", sans-serif;
  background-color: #fff;
}

/* Center-align the main content */
main {
  text-align: center;
  margin: 0 auto;
}

/* Style the center content */
.center-content {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  min-height: 100vh;
}

.center-content img {
  max-width: 150px;
  margin-bottom: 20px;
}

.homeApplyButton {
  background-color: #36bbbd;
  margin-top: 50px;
}

a {
  text-decoration: none;
}

.center-content h1 {
  font-size: 75px;
  margin: 10px 0;
  color: #000;
  text-align: center;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  font-family: Inter;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  letter-spacing: 0.1rem;
}

.center-content h2 {
  font-size: 24px;
  margin: 10px 0;
  color: #000;
  text-align: center;
  font-family: Inter;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 0.1875rem;
}

.center-content h3 {
  margin: 50px 0 0 0;
  color: rgba(0, 0, 0, 0.8);
  text-align: center;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 0.7rem;
}

/* Style for Apply and Profile buttons */
.apply-button,
.profile-button {
  display: inline-block;
  padding: 0.75rem 2rem;
  background-color: #36bbbd;
  color: #fff;
  text-decoration: none;
  font-weight: bold;
  font-size: 15px;
  margin-top: 20px;
  border-radius: 5px;
  margin-left: 10px;
  margin-right: 2rem;
  box-shadow: 0px 4px 4px #174c4d;
}

.apply-button:hover,
.profile-button:hover {
  background-color: #555;
  transition-duration: 0.75s;
}

/* Style for aligning Apply and Profile buttons to the right */
.button-container {
  text-align: right;
  margin-right: 20px;
}

.vector-1 {
  position: absolute;
  z-index: -1;
}

.vector-2 {
  position: absolute;
  z-index: -1;
  right: 0%;
  top: 15rem;
}

.vector-3 {
  position: absolute;
  z-index: -1;
  left: 0%;
  top: 30rem;
}

.vector-4 {
  /* Vector */

  position: absolute;
  left: 0px;
  top: 82rem;
  z-index: -1;
}

.vector-5 {
  position: absolute;
  right: 0px;
  top: 82rem;
  z-index: -1;
}

.why {
  position: relative;
}

.text img {
  position: absolute;
  top: 10%;
  height: 100px;
}

.text {
  padding-top: 2.5rem;
  font-size: 80px;
  text-align: center;
  margin: 70px 0;
  color: #000;
  text-align: center;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  font-family: Inter;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  letter-spacing: 0.1rem;
}

.ccs {
  color: #36bbbd;
}

.why-img {
  width: 120px;
  height: auto;
}

.item-content {
  padding: 23px 0px 0px;
  word-wrap: break-word;
  word-break: keep-all;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  --color: rgba(30, 30, 30);
  --bgColor: rgba(245, 245, 245);
}

h1.Recruitment {
  color: black;
  font-size: 60px;
  font-weight: 700;
  letter-spacing: 2px;
  word-wrap: break-word;
  text-align: center;
  margin: 100px 0px 50px 0px;
}

ul {
  --col-gap: 3rem;
  --row-gap: 7rem;
  --line-w: 0.25rem;
  display: grid;
  grid-template-columns: var(--line-w) 1fr;
  grid-auto-columns: max-content;
  column-gap: var(--col-gap);
  list-style: none;
  width: min(60rem, 90%);
  margin-inline: auto;
}

/* line */
ul::before {
  content: "";
  grid-column: 1;
  grid-row: 1 / span 20;
  background: rgb(225, 225, 225);
  border-radius: calc(var(--line-w) / 2);
  width: 5px;
}

/* columns*/

/* row gaps */
ul li:not(:last-child) {
  margin-bottom: var(--row-gap);
}

/* card */
ul li {
  grid-column: 2;
  --inlineP: 1.5rem;
  margin-inline: var(--inlineP);
  grid-row: span 2;
  display: grid;
  grid-template-rows: min-content min-content min-content;
}

/* date */
ul li .date {
  --dateH: 3rem;
  height: var(--dateH);
  margin-inline: calc(var(--inlineP) * -1);

  text-align: center;
  background-color: #36bbbd;

  color: white;
  font-size: 1.25rem;
  font-weight: 700;

  display: grid;
  place-content: center;
  position: relative;

  border-radius: calc(var(--dateH) / 2) 0 0 calc(var(--dateH) / 2);
}

/* date flap */
ul li .date::before {
  content: "";
  width: var(--inlineP);
  aspect-ratio: 1;
  background: #36bbbd;
  background-image: linear-gradient(rgba(0, 0, 0, 0.2) 100%, transparent);
  position: absolute;
  top: 100%;

  clip-path: polygon(0 0, 100% 0, 0 100%);
  right: 0;
}

/* circle */
ul li .date::after {
  content: "";
  position: absolute;
  width: 2rem;
  aspect-ratio: 1;
  background: var(--bgColor);
  border: 0.5rem solid #36bbbd;
  border-radius: 50%;
  top: 50%;

  transform: translate(50%, -50%);
  right: calc(100% + var(--col-gap) + var(--line-w) / 2);
}

/* title descr */
ul li .title,
ul li .descr {
  background: var(--bgColor);
  position: relative;
  padding-inline: 1.5rem;
}
ul li .title {
  overflow: hidden;
  padding-block-start: 1.5rem;
  padding-block-end: 1rem;
  font-weight: 500;
}
ul li .descr {
  padding-block-end: 1.5rem;
  font-weight: 300;
}

/* shadows */
ul li .title::before,
ul li .descr::before {
  content: "";
  position: absolute;
  width: 90%;
  height: 0.5rem;
  background: rgba(0, 0, 0, 0.5);
  left: 50%;
  border-radius: 50%;
  filter: blur(4px);
  transform: translate(-50%, 50%);
}
ul li .title::before {
  bottom: calc(100% + 0.125rem);
}

ul li .descr::before {
  z-index: -1;
  bottom: 0.25rem;
}

@media (min-width: 40rem) {
  ul {
    grid-template-columns: 1fr var(--line-w) 1fr;
  }
  ul::before {
    grid-column: 2;
  }
  ul li:nth-child(odd) {
    grid-column: 1;
  }
  ul li:nth-child(even) {
    grid-column: 3;
  }

  /* start second card */
  ul li:nth-child(2) {
    grid-row: 2/4;
  }

  ul li:nth-child(odd) .date::before {
    clip-path: polygon(0 0, 100% 0, 100% 100%);
    left: 0;
  }

  ul li:nth-child(odd) .date::after {
    transform: translate(-50%, -50%);
    left: calc(100% + var(--col-gap) + var(--line-w) / 2);
  }
  ul li:nth-child(odd) .date {
    border-radius: 0 calc(var(--dateH) / 2) calc(var(--dateH) / 2) 0;
  }
}

@media (max-width: 40rem) {
  .vectors {
    opacity: 0.2;
  }
  .center-content h1 {
    font-size: 40px;
  }

  .center-content h2 {
    font-size: 16px;
  }

  .center-content h3 {
    font-size: 10px;
    margin-top: 25px;
    letter-spacing: 0.4rem;
  }
  .text {
    font-size: 45px;
  }

  h1.Recruitment {
    font-size: 35px;
    letter-spacing: normal;
  }
}

footer {
  background-color: #f0f0f0;
  padding: 50px;
  bottom: 0;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.social-icons {
  font-size: 40px;
  margin: 0 10px;
  color: #000000;
  text-decoration: none;
}

.faq {
  margin: auto;
  margin-bottom: 50px;
}

.faq-box {
  width: 75vw;
  height: 50vh;
  padding: 20px;
  border-radius: 20px;
  border: 5px solid #36bbbd;
  margin: auto;
}
