  .specialButt{
    transition: ease 800ms;
    border: none;
    background-color: transparent;
    margin-top: 4%;
    cursor: pointer;
  }
  
  .movingButt{
    font-size: 32px;
    animation: wave 1s infinite ease-in ;
    font-weight: 300;
  }
  .staticButt{
    margin-top: -88%;
    font-size: 72px;
    font-weight: 300;
  }
  .trans{
    transform: rotate(180deg);
  }
 
  @keyframes wave {
    0%, 100% {
      transform: translateY(-6px);
    }
    12.5%, 87.5% {
      transform: translateY(-3px);
    }
    25%, 75% {
      transform: translateY(0px);
    }
    37.5%, 62.5% {
      transform: translateY(3px);
    }
    50% {
      transform: translateY(6px);
    }
  }




