@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');

.sideBg{
  background: url('./Assets/Bg23.png');
  background-repeat:no-repeat ;
  background-size: cover;
  background-position: center;
}

.sidebar {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  display: flex;
  min-height: 100vh;
  width: 20vw;
  color: #fff;
  padding: 20px;
  box-sizing: border-box;
  flex-direction: column;
  align-items: center;
}

.logo {
  max-width: 30%;
  height: auto;
  margin-top: 7%;
}

.menuLine {
  width: 80%;
  border-top: 1px solid black;
  margin: 30px 0;
}

.UserName{
  display: flex;
  justify-content: space-evenly;
  width: 80%;
  align-items: center;
  color: #000;
}

.UserName>div>h1{
  line-height: 28px;
  font-family: 'Poppins', sans-serif;
}
.UserName>div>p{
  font-size: 12px;
  margin-top: 2%;
  font-weight: 400;
  color: #03a1a3;
}

.Icon,.selectedIcon{
  padding: 2%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 26px;
  font-weight: 700;
  color:#36BBBD;
  border: 3px solid #36BBBD;
  border-radius: 5px;
  background: transparent;
  transition: ease-in-out 150ms;
  cursor: pointer;
}
.Icon:hover{
  color: white;
  scale: 1.15;
  background-color: #36BBBD;
}
.selectedIcon{
  color: white;
  background-color: #36BBBD;  
}


.menuButtons {
  display: flex;
  margin-top: 10%;
  width: 80%;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: center;
  text-align: center; 
}

.menuButton,.selected {
  border: none;
  background-color: transparent;
  cursor: pointer;
  color: #636363;
  padding: 10px 0;
  font-size: 18px;
  font-weight: normal;
  transition: ease-in-out 150ms;
  width: 85%;
  background: #f2fcff94;
  box-shadow: 0 8px 8px  rgba(201, 201, 201, 0.315);
  backdrop-filter: blur( 5px );
  border-radius: 10px;
  border: 1px solid rgba( 255, 255, 255, 0.18 );
  margin-block: 0.8%;
}
.menuButton:hover {
    color: #1a9c9e;
    scale: 1.12;
    margin-block: 3%;
}

.selected {
  font-weight: 700;
  width: 100%;
  background-color: #36BBBD;
  color: white;
  padding: 8% 0% ;
  transition: ease-in-out 150ms;
  z-index: 20;
  margin-block: 2%;
  border-radius: 10px;
}


.footerText {
  position: absolute;
  font-size: 25px;
  letter-spacing: 5px;
  bottom: 10px;
  text-align: center;
  color: #36BBBD;
  font-weight: 600;
}




.buttCont{
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20%;
  bottom: 10%;
}

.submitButt{
  width: 70%;
  padding: 5px 10px;
  font-size: 19px;
  font-weight: 700;
  cursor: pointer;
  background-color: #fff;
  color: #36bbbd;
  border: 3px #36bbbd solid;
  border-radius: 15px;
  transition: ease-in-out 100ms;
}
.submitButt>p{
  margin: 0%;
  padding: 0%;
}

.submitButt:hover{
  scale:1.08;
  background-color: #36bbbd;
  color: white;
  box-shadow: 0 0 10px #36bbbd;
}




