.pastApplications{
    width: 100%;
    height: 100vh;
    position: relative;
    z-index: 0;
    
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}

.pastApplicationBanner{
    height: 50vh;
    width: 100%;
    background-color: #36BBBD;

    position: absolute;
    top: 0;
    z-index: -100;
}

.pastApplicationCont{
    width: 75%;
    height: 500px;

    background-color: #f0f0f0;

    border-radius: 20px;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.3);
    z-index: 100;

    display: flex;
    flex-direction: column;

    /* -moz-box-sizing: border-box; 
    -webkit-box-sizing: border-box; 
    box-sizing: border-box; */

    padding: 30px 30px;
}

.pastApplicationCont > .formRow{
    
    width: 100%;
    min-height: 60px;
    gap: 100px;
    
}

.pastApplicationCont > .formRow > .formData:nth-child(1){
    background-color: rgb(187, 187, 187);
    flex-basis: 25%;
}

.pastApplicationCont > .formRow > .formData:nth-child(2){
    background-color: rgb(187, 187, 187);
    flex-basis: 75%;
}

.pastApplicationCont > .formRow > .formData{
    display: flex;
    justify-content: center;

    font-family: inter;
    font-weight: bold;

    padding: 0 30px;
}