.login {
  height: 100vh;
  width: 100%;

  display: flex;
  align-items: center;
  justify-content: center;

  background-color: bl;

  background-image: url("./Assets/bgbgbgbg.jpg");
  background-size: cover;
  background-position: center center;
}

.loginCont {
  height: calc(100% - 150px);
  width: calc(100% - 400px);
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(10px);

  border-radius: 20px;

  display: flex;
  flex-direction: row;
}

@media only screen and (max-width: 600px) {
  .loginCont {
    height: calc(100% - 250px);
    width: calc(100% - 20px);
  }
}

@media only screen and (min-width: 600px) {
  .loginCont {
    height: calc(100% - 150px);
    width: calc(100% - 50px);
  }
}

@media only screen and (min-width: 768px) {
  .loginCont {
    height: calc(100% - 150px);
    width: calc(100% - 100px);
  }
}

@media only screen and (min-width: 992px) {
  .loginCont {
    height: calc(100% - 150px);
    width: calc(100% - 250px);
  }
}

@media only screen and (min-width: 1200px) {
  .loginCont {
    height: calc(100% - 150px);
    width: calc(100% - 400px);
  }
}

.loginLeft {
  position: relative;
  width: 50%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  font-family: inter;
  background-size: cover;
  background-position: center;
  /* background-color: teal; */
  border-radius: 20px 0px 0px 20px;
}

.loginLeft::after {
  border-radius: 20px 0px 0px 20px;
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: black;
  opacity: 80%;
  z-index: 10;
}

.loginLeft > p {
  color: white;
  z-index: 100;
  margin-top: 20px;
  text-decoration: underline;
}

.loginRight {
  color: white;
  border-radius: 0px 20px 20px 0px;
  width: 50%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/* .login-bg {
  position: absolute;
  heig

} */

.loginRight > h1 {
  margin-top: 30px;
}

.loginForm > p {
  margin: 10px 0;
}

.loginForm {
  width: 70%;
  font-family: inter;
}

.loginFromGroup {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-top: 30px;
}

.loginFromGroup > input {
  height: 40px;
  border-radius: 5px;
  border: 0px solid;
  outline: 0px solid;
  background-color: lightgrey;

  padding: 0 10px;
  font-family: inter;
  font-size: 14px;

  margin-top: 5px;
}

.loginFromGroup > input:active {
  background-color: lightgrey;
}

.loginFormGroup > .button {
  width: 100%;
  border-radius: 8px;
  text-align: center;

  border: 0px solid;
  background-color: #36bbbd;
  color: white;
  font-size: 18px;
  font-family: inter;
  font-weight: bolder;
  letter-spacing: 2px;
  margin-top: 30px;
  margin-bottom: 10px;
}

.loginSocial {
  width: 100%;
  display: flex;
  font-size: 30px;
  color: white;
  justify-content: space-evenly;
  z-index: 20;

  margin-top: 10px;
}

.loginSocial > * {
  cursor: pointer;
  transition-duration: 200ms;
  text-decoration: none;
  color: white;
}

.loginSocial > *:hover {
  transform: translateY(-3px);
}

.logo {
  z-index: 20;
}

@media (max-width: 780px) {
  .loginLeft {
    display: none;
  }
  .loginRight {
    width: 100%;
    border-radius: 20px;
  }
}
