  @property --x {
    syntax: '<angle>';
    inherits: true;
    initial-value: 1deg;
  }
  
  @property --c {
    syntax: '<color>';
    inherits: true;
    initial-value: #0000;
  }
  
  .neon {
    --x: 1deg;
    --c: #fff;
    font-size: 3rem;
    appearance: none;
    position: relative;
    aspect-ratio: 2;
    border-radius: 100em;
    background: 
      linear-gradient(to bottom right, rgba(255, 255, 255, 0.067), #ffffff00),
      #ffffff;
    width: 3em;
    box-shadow:
      inset 0 0 0.25em -0.25em rgba(161, 161, 161, 0.533),
      inset 0.05em 0.05em 0.2em #36bbbd8a,
      inset -0.05em -0.05em 0.15em 0.05em #ccc1,
      0 0 0 0.16em #545153
      ;
    transition: --x 0.5s, --c 0.35s, box-shadow 0.5s;
    filter: drop-shadow(0 0 0.2em var(--c));
    cursor: pointer;
    scale: 0.5;
    
    &::before {
      content: "";
      position: absolute;
      height: 80%;
      aspect-ratio: 1;
      top: 50%;
      left: 25%;
      transform: translate(-50%, -50%);
      background: #545153;
      box-shadow:
        inset -0.05em -0.05em 0.1em #0008,
        inset 0.05em 0.05em 0.1em #ffffff22,
        inset 0.05em 0em 0.1em -0.065em var(--c),
        0.1em 0.1em 0.15em #000c;
      border-radius: 50%;
      transition: left 0.5s, box-shadow 0.15s;
    }
    
    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: calc(100% + 0.25em);
      height: calc(100% + 0.25em);
      border: 0.1em solid #0ff;
      border-radius: 100em;
      -webkit-mask: conic-gradient(from calc(270deg - var(--x)), #000 calc(2 * var(--x)), #0001 0);
      box-shadow: 0 0 0 2em #0000; /* hack for safari */
    }
    
    &:hover {
      --x: 3deg;
      --c: #0ff5;
      box-shadow:
        inset 0 0 0.25em -0.25em #0008,
        inset 0.05em 0.05em 0.2em #000811,
        inset -0.05em -0.05em 0.15em 0.05em #ccc1,
        0 0 0.05em 0.075em #545153
        ;
    }
    
    &:checked {
      --x: 180deg;
      --c: #0fff;
      box-shadow:
      inset 0 0 0.25em -0.25em #0008,
      inset 0.05em 0.05em 0.2em #000811,
      inset -0.05em -0.05em 0.15em 0.05em #ccc1,
      0 0 0.1em 0.05em #545153
      ;
      
      &::before {
        left: 75%;
        box-shadow:
          inset -0.05em -0.05em 0.1em #0008,
          inset 0.05em 0.05em 0.1em #ffffff22,
          inset -0.05em 0em 0.1em -0.035em var(--c),
          0.1em 0.1em 0.15em #000c;
      }
    }
    
    &[disabled] {
      opacity: 0.25;
    }
  }
  
  @media print {
    .neon {
      background: none;
      
      &, &::before, &::after {
        -webkit-print-color-adjust: exact;
        print-color-adjust: exact;
      }
    }
  }
  
  @media (prefers-reduced-motion) {
    .neon, .neon::before, .neon::after {
      transition: none !important;
      animation: none !important;
    }
  }






  .copyText {
    position: relative;
    height: 5vh;
    padding: 15px;
    background: #fff;
    border: 1px solid #ddd;
    border-radius: 10px;
    display: flex;
    scale: 0.7;
    gap:12px;
    align-items: center;
    transition: ease-in-out 350ms;
  }
  .copyText a {
    padding: 10px;
    font-size: 18px;
    color: #555;
    border: none;
    outline: none;
    transition: ease-in-out 150ms;
  }
  .copyText>a:hover {
    scale: 1.15;
  }
  .copyText button {
    padding: 10px;
    background: #5784f5;
    color: #fff;
    font-size: 18px;
    border: none;
    outline: none;
    border-radius: 10px;
    cursor: pointer;
  }
  
  .copyText button:active {
    background: #809ce2;
  }
  .copyText button:before {
    content: "Copied";
    position: absolute;
    top: -45px;
    right: 0px;
    background: #5c81dc;
    padding: 8px 10px;
    border-radius: 20px;
    font-size: 15px;
    display: none;
  }
  .copyText button:after {
    content: "";
    position: absolute;
    top: -20px;
    right: 25px;
    width: 10px;
    height: 10px;
    background: #5c81dc;
    transform: rotate(45deg);
    display: none;
  }
  .copyText.active button:before,
  .copyText.active button:after {
    display: block;
  }








  /* LOADER */

  .loaderCont{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 77vh;
  }
  .loader {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    display: inline-block;
    position: relative;
    border: 3px solid;
    border-color: #36BBBD #36BBBD transparent transparent;
    box-sizing: border-box;
    animation: rotation 1s linear infinite;
    scale: 1.5;
  }
  .loader::after,
  .loader::before {
    content: '';  
    box-sizing: border-box;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    border: 3px solid;
    border-color: transparent transparent #FF3D00 #FF3D00;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    box-sizing: border-box;
    animation: rotationBack 0.5s linear infinite;
    transform-origin: center center;
  }
  .loader::before {
    width: 32px;
    height: 32px;
    border-color: #36BBBD #36BBBD transparent transparent;
    animation: rotation 1.5s linear infinite;
  }
      
  @keyframes rotation {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  } 
  @keyframes rotationBack {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(-360deg);
    }
  }
      







