/* new-applications-style.css */

    .newApps{
        display: flex;
    }

  .content {
    padding: 0;
    margin-top: 0;
    width: 80vw;
    height: 100vh;
    overflow-y: hidden;
  }
  
  .applicationsHeader {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2% 5%;
    box-shadow: inset 0 0 5px #ddd;
    /* border-bottom: 1px solid #ddd; */
    background: url('../../Assets/BGbb.png');
    background-repeat:no-repeat ;
    background-size: 20;
    background-position: right bottom;
  }
  
  
  .heading{
    font-size: 40px;
  }

  .searchBar {
    padding: 10px 20px;
    border-radius: 20px;
    background-color: #f4f4f4;
    display: flex;
    align-items: center;
  }
  
  .searchIcon {
    margin-right: 10px;
    color: #737373;
  }
  
  .searchInput {
    background-color: #f4f4f4;
    border: none;
    padding: 10px;
    font-size: 16px;
    border-radius: 5px;
    outline: none;
  }
  
  .applicationsList {
    flex: 1;
    overflow-y: auto;
  }

  .applications{
    height:83vh;
    overflow-y: auto;
    padding: 1% 1%;
  }
  
  .applicationItem {
    margin: 10px;
    border-radius: 20px;
    background: rgba(239, 241, 241, 0.5);
    box-shadow: 0 8px 12px 0 rgba(224, 224, 224, 0.712);
    backdrop-filter: blur( 15px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px;
    border-bottom: 1px solid #ddd;
    cursor: pointer;
  }
  
  .applicationItem img {
    border-radius: 50%;
    width: 4%;
    height: auto;
    padding: 10px;
  }
  
  .userName {
    width: 35%;
    padding: 10px 30px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 20px;
    font-weight: 600;
  }
  
  .userRollno {
    width: 20%;
    padding: 10px 30px;
    font-size: 19px;
    font-weight: 400;
  }
  
  .applicationItemDate {
    width: 37%;
    padding: 10px 30px;
    font-size: 18px;
    font-weight: 500;
    text-align: right;
  }
  
  .applicationLink {
    color: black;
    text-decoration: none;
  }
  
  .applicationLink:hover {
    text-decoration: none;
  }
  
  ::-webkit-scrollbar-thumb {
    background: #888;
  }
  










  /* Page Change Container */
  .pageChangeCont{
    display:flex ;
    justify-content: center;
    align-items: center;
    margin: 1% 0 0% 0;
  }
  .changeArrows{
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    font-size: 18px;
    font-weight: 700;
    padding: 10px;
    margin: 0.3rem;
    border: none;
    background: #36BBBD;
    color: #fff;
    cursor: pointer;
    transition: ease-in-out 150ms;
  }

  .enabled:hover{
    scale: 1.1;
  }

  .currentPage{
    background: #e9e9e9;
    color: #000;
    margin: 0 1%;
    font-size: 16px;

    padding: 14px 22px;
    border-radius: 50%;
  }

  .changeArrows[disabled]{
    background-color: #cccccc;
    color: #666666;
    cursor: default;
  }











  


  /* LOADER */

  .loaderCont{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 77vh;
  }
  .loader {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    display: inline-block;
    position: relative;
    border: 3px solid;
    border-color: #36BBBD #36BBBD transparent transparent;
    box-sizing: border-box;
    animation: rotation 1s linear infinite;
    scale: 1.5;
  }
  .loader::after,
  .loader::before {
    content: '';  
    box-sizing: border-box;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    border: 3px solid;
    border-color: transparent transparent #FF3D00 #FF3D00;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    box-sizing: border-box;
    animation: rotationBack 0.5s linear infinite;
    transform-origin: center center;
  }
  .loader::before {
    width: 32px;
    height: 32px;
    border-color: #36BBBD #36BBBD transparent transparent;
    animation: rotation 1.5s linear infinite;
  }
      
  @keyframes rotation {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  } 
  @keyframes rotationBack {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(-360deg);
    }
  }
      





















