/* AppDet.css */
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");

.overlay{
  position: fixed;
  top:0;
  left:0;
  z-index: 2000;
  height: 100vh;
  width: 100%;
  background:transparent;
}
.popup{
  position: fixed;
  z-index: 4000;
  height: 75vh;
  top:10%;
  left: 12.5%;
  width: 75%;
  height: 83vh;
  border-radius: 20px;
  background: rgba(24, 177, 204, 0.35);
  box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
  backdrop-filter: blur( 19.5px );
  -webkit-backdrop-filter: blur( 19.5px );
  padding: 2% 0 ;
}

.CrossBtn{
  right:1.4%;
  top:3%;
  font-size: 30px;
  cursor: pointer;
  transition: ease-in-out 150ms;
  border-radius: 50%;
  padding: 0%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed; /* Ensure the button stays fixed */
  z-index: 100000; /* Set a higher z-index to ensure it appears above the popup */
}
.CrossBtn:hover{
  scale: 1.3;
  box-shadow: 0 0 10px white;
}

