.searchCont {
    padding: 10px;
    border-radius: 50px;
    background-color: #f4f4f4;
    display: flex;
    align-items: center;
  }
  
  .searchIcon {
    color: #737373;
    cursor: pointer;
    font-size: 28px;
    transition: ease-in-out 150ms;
  }
  .searchIcon:hover {
    scale: 1.45;
  }




  @import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");

.overlay{
  position: fixed;
  top:0;
  left:0;
  z-index: 100;
  height: 100vh;
  width: 100%;
  background:transparent;
}
.popup{
  position: fixed;
  z-index: 1000;
  left: 12.5%;
  width: 75%;
  min-height: 20vh;
  max-height: 94vh;
  border-radius: 0 0 20px 20px;
  background: rgb(238,238,255);
  background: linear-gradient(0deg, rgba(212, 212, 212, 0.459) 23%, rgba(141,222,238,1) 80%);
  backdrop-filter: blur( 9px );
  -webkit-backdrop-filter: blur( 3px );
  padding: 2% 0 ;
  /* display: flex;
  justify-content: center; */
}

.CrossBtn{
  right:1.4%;
  top:3%;
  font-size: 30px;
  cursor: pointer;
  transition: ease-in-out 150ms;
  border-radius: 50%;
  padding: 0%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed; /* Ensure the button stays fixed */
  z-index: 4000; /* Set a higher z-index to ensure it appears above the popup */
}
.CrossBtn:hover{
  scale: 1.3;
  box-shadow: 0 0 10px white;
}



.popup>h1{
  text-align: center;
  color: #fff;
  margin-bottom: 2%;
}

.centerAlign{
  display: flex;
  justify-content: center;
}





.SearchBar{
  background-color: #f4f4f4c2;
  width:60%;
  border-radius: 25px;
  padding:1% 2%;
  margin-bottom: 2%;
  display: flex;
  align-items: center;
}
.searchInput {
  color: black;
  background-color: transparent;
  padding: 0  2%;
  width: 100%;
  border: none;
  font-size: 16px;
  outline: none;
}
.searchInput::placeholder{
  color: black;
}






.applications{
  min-height: 0vh;
  max-height:77vh;
  overflow-y: auto;
  padding: 0% 4%;
  margin: 0%;
  }







  
/* LOADER */

.loaderCont{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 77vh;
  }
  .loader {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  display: inline-block;
  position: relative;
  border: 3px solid;
  border-color: #36BBBD #36BBBD transparent transparent;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
  scale: 1.5;
  }
  .loader::after,
  .loader::before {
  content: '';  
  box-sizing: border-box;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  border: 3px solid;
  border-color: transparent transparent #FF3D00 #FF3D00;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  box-sizing: border-box;
  animation: rotationBack 0.5s linear infinite;
  transform-origin: center center;
  }
  .loader::before {
  width: 32px;
  height: 32px;
  border-color: #36BBBD #36BBBD transparent transparent;
  animation: rotation 1.5s linear infinite;
  }
    
  @keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
  } 
  @keyframes rotationBack {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-360deg);
  }
  }
    
  