.profileInput{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: lightgray;
}
.backBanner{
    background-color: #36BBBD;
    height: 400px;
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;

    text-shadow: 3px 3px 5px rgba(59, 59, 59, 0.5);
    font-family: inter;
}

.profileInputHeading{
    text-transform: uppercase;

    font-size: 90px;
    letter-spacing: 3px;
    margin-top: 50px;
}

.profileInputYear{
    font-weight: 400;
    font-size: 30px;
}

.profileFormCont{
    margin-top: -150px;
    width: 100%;
    
    display: flex;
    justify-content: center;
}

.profileFormWrapper{
    
    display: flex;
    flex-direction: column;
    align-items: center;
    
    background-color: #f1f1f1;
    width: calc(100% - 200px);
    height: inherit;
    
    border-radius: 15px;
    margin-bottom: 200px;
}

.profileFormWrapper > h1{
    font-size: 40px;
    margin: 100px 0;
}

.profileForm{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    
}

.profileForm > .button{
    background-color: #36BBBD;
    font-weight: bold;
    font-size: 16px;
    margin-top: 20px;

    margin-bottom: 40px;
    border: 0px solid;
}

.profileForm > .button:disabled{
    background-color: grey;
}

.formRow{
    width: calc(75%);
    display: flex;
    justify-content: space-between;
    gap: 100px;

    margin-bottom: 30px;
}

.formData{
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    width: 100%;
}

.formData > label{
    font-family: inter;
    font-weight: bold;

    margin-bottom: 7px;

    margin-left: 1px;
}

.formData > input, select{

    -moz-box-sizing: border-box; 
    -webkit-box-sizing: border-box; 
    box-sizing: border-box;
    
    flex-grow: 1;

    font-family: inter;
    font-weight: 500;
    font-size: 15px;

    height: 40px;
    width: 100%;
    background-color: lightgray;

    outline: 0px solid;
    border: 0px solid;

    border-radius: 5px;

    padding: 0 20px;
}

.formData > textarea{
    -moz-box-sizing: border-box; 
    -webkit-box-sizing: border-box; 
    box-sizing: border-box; 

    font-family: inter;
    font-size: 15px;
    width: 100%;
    background-color: lightgray;

    outline: 0px solid;
    border: 0px solid;

    border-radius: 5px;

    padding: 20px 20px;
}

.formData > input::placeholder{
    font-weight: bold;
    font-family: inter;
}

.nonTech{
    display: flex;
    justify-content: space-between;

    gap: 100px;
}

.nonTech > .left, .right{
    width: 50%;
}


.checkData{
    margin: 20px 0;
    display: flex;
    align-items: center;
}

.checkData > label{
    font-family: inter;
    font-weight: bold;
}

.checkData > input{
    margin-right: 20px;
    height: 20px;
    width: 20px;
}

.checkData > input:checked{
    background-color: #36BBBD;
}

span.error{
    color: rgb(255, 0, 64);
    font-size: 12px;
}